.App {
    text-align: center;
  }
  
.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}
  
.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}
  
.App-title {
    font-size: 1.5em;
    color: #61dafb;
}
  
.App-intro {
    font-size: large;
}
  
.App-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 20px;
    /* justify-content: space-between; */
}
  
.Toolbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
  
.Menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 0;
    width: 300px; 
    -ms-flex-align: center; 
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
  
.Menu li {
    list-style-type: none;
}
  
@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
  
@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


